.wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 8px;
    width: 100%;
    max-height: min((100vh - 96px) - 60px, 734px);
    min-height: 100px;
    border-radius: 8px;
    box-shadow: rgb(0 0 0/ 12%) 0px 2px 12px;
    background-color: var(--background-color-secondary);
    overflow-y: scroll;
}
